* {
  box-sizing: inherit;
}

.settings_page_awesome {
  background: #f6f6f6;
  font-size: 14px;
  position: relative;
  box-sizing: border-box;
  font-family: Helvetica Neue,Helvetica,Arial,Lucida Grande,sans-serif;
}

#wpcontent {
  padding-left: 0;
}

.auto-fold {
  #wpcontent {
    padding-left: 0;
  }
}

#codeinwp-awesome-plugin {
  .components-placeholder {
    background: #f6f6f6;
    margin-top: 25vh;
    margin-bottom: 25vh;
  }

  .c-header--admin-header{
    padding: 20px 10px;
    background-color: #ffffff;
    box-shadow: 0 1px 0 rgba(213,213,213,0.5),0 1px 2px #eee;

    .c-header__container {
      margin: 0 auto;
      max-width: 750px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  .codeinwp-main {
    margin-left: auto;
    margin-right: auto;
    max-width: 750px;

    .components-panel__body {
      margin: 25px 0;
      background: #fff;
      border: 1px solid #e2e4e7;

      .components-panel__body-toggle {
        border-bottom: 1px solid #d6e2ed;
      }

      .components-panel__row {
        padding: 10px;
        margin: 0;

        .components-base-control {
          .components-base-control__field {
            display: flex;
            align-items: center;
            margin-bottom: 10px;
          }

          &.c-form__input--text-admin {
            width: 100%;

            .components-base-control__field {
              display: block;

              label {
                font-weight: 600;
                margin-bottom: 10px;
              }

              input {
                border: 1px solid #b7c9d9;
                border-radius: 3px;
                font-size: 14px;
                padding: 9px 15px;
                width: 100%;
                box-shadow: none;
                height: 40px;
                margin: 0 0 10px 0;
                color: #444;

                &:disabled {
                  background: #f6f6f6;
                  border-color: #eeeeee;
                  color: #bbbbbb;
                }

                &:focus {
                  border-color: #00a0d2;
                  box-shadow: 0 0 0 1px #00a0d2;
                  outline: 2px solid transparent;
                  outline-offset: -2px;
                }
              }

              .codeinwp-text-field-button-group {
                display: flex;
                align-items: center;
                margin: 2px 0 15px;

                .is-primary {
                  padding: 2px 20px;
                  height: auto;
                  font-size: 14px;
                  margin-right: 10px;
                }
              }
            }
          }
        }
      }
    }

    .codeinwp-info {
      padding: 0 10px 10px;

      h2 {
        font-weight: 300;
        margin-bottom: 10px;
      }

      p {
        margin: 0 0 1.5em 0;
      }

      .codeinwp-info-button-group {
        display: flex;

        .is-default {
          padding: 2px 20px;
          height: auto;
          font-size: 14px;

          &:first-child {
            margin-right: 15px;
          }
        }
      }
    }
  }
}
